// Slider right to left

.swiper-rtl {
  overflow: hidden;

  @media screen and (max-width: 1023px) {
    height: 297px;
  }

  @include tablet {
    height: 258px;
  }

  @include desktop {
    height: 322px;
  }

  & .swiper-slide.swiper-slide-prev,
  & .swiper-slide.swiper-slide-active,
  & .swiper-slide.swiper-slide-next {
    width: 245px;
    height: 245px;
    overflow: hidden;

    @include tablet {
      width: 194px;
      height: 194px;
    }

    @include desktop {
      width: 254px;
      height: 254px;
    }
  }

  & .swiper-img {
    width: 245px;
    height: 245px;

    @include tablet {
      width: 194px;
      height: 194px;
    }

    @include desktop {
      width: 254px;
      height: 254px;
    }
  }

  & .swiper-button__wraper {
    position: absolute;
    bottom: 0;
    display: flex;
    gap: 32px;

    @media screen and (max-width: 1023px) {
      left: 220;
    }
  }

  & .swiper-button__next {
    fill: var(--arrow-black-cl);
    transition-property: fill;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);

    &:hover,
    &:focus {
      fill: var(--accent-cl);
    }
  }

  & .swiper-button__prev {
    fill: var(--arrow-black-cl);
    transition-property: fill;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);

    &:hover,
    &:focus {
      fill: var(--accent-cl);
    }
  }
}

