// Details-restaurant slider

.details-restaurant__box {
  @media screen and (max-width: 1023px) {
    margin-left: 25px;
  }

  @include tablet {
    display: flex;
    justify-content: left;
    flex-direction: row-reverse;
  }
}

.details-restaurant__swiper {
  position: relative;
  margin-bottom: 32px;
  overflow: hidden;

  @media screen and (max-width: 1023px) {
    width: 374px;
  }

  @include tablet {
    margin-bottom: 0;
  }

  & .swiper-wrapper {
    margin-bottom: 48px;

    @include tablet {
      margin-bottom: 56px;
    }
  }

  & .swiper-slide {
    width: auto !important;
    overflow: hidden;
  }

  & .swiper-slide.swiper-slide-duplicate,
  & .swiper-slide.swiper-slide-prev,
  & .swiper-slide.swiper-slide-active,
  & .swiper-slide.swiper-slide-next {
    height: 270px;
  }

  & .swiper-button__wraper {
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    gap: 32px;
  }

  & .swiper-button__next {
    fill: var(--arrow-white-cl);
    transition-property: fill;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);

    &:hover,
    &:focus {
      fill: var(--accent-cl);
    }
  }

  & .swiper-button__prev {
    fill: var(--arrow-white-cl);
    transition-property: fill;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);

    &:hover,
    &:focus {
      fill: var(--accent-cl);
    }
  }
}

